import React from 'react';
import photo from './Photo';
import Photo from './Photo';

class PhotoGrid extends React.Component{
    render() {
        return (
            <div className='photo-grid'>
                {this.props.posts.map( (post,i) => <Photo 
                    {...this.props} key={i} i={i} post={post}/>)}
            </div>
        )
    }
};

export default PhotoGrid;


